.program {
  padding: 100px 0;
}

.program h1 {
  color: #5b6ceb;
  text-transform: uppercase;
  font-family: 'OpenSansBold', sans-serif;
  font-size: 30px;
  line-height: 60px;
  text-align: center;
}

.program .section-subtitle {
  color: #777;
  font-family: 'FreeStyleRegular', sans-serif;
  font-size: 40px;
  line-height: 40px;
  text-align: center;
  padding-bottom: 70px;
}

.program__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: 300px;
  grid-column-gap: 30px;
  grid-row-gap: 25px;
}

.program__item .wraper {
  border-radius: 8px;
  box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.1);
  display: flex;
  height: 300px;
}

.program__item .program-img {
  background-color: #5b6ceb;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  width: 270px;
  flex-shrink: 0;
}

.program__item .program-img img {
  height: 100%;
  object-fit: cover;
  width: 100%;
}

.program__item .program-info {
  padding: 45px 29px;
}

.program__item .program-info .title {
  color: #777;
  font-size: 20px;
  font-family: 'OpenSansBold', serif;
  height: 50px;
  text-transform: uppercase;
}

.program__item .program-info .description {
  color: #555;
  line-height: 24px;
  padding-bottom: 10px;
}

.program__item .program-info .couch {
  color: #999;
}